import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
Vue.use(VueRouter);

const routes = [
  {
    path: '/:email',
    name: 'Home',
    component: Home,
  },
  {
    path: '/verification/sms/:email/:promptNumber',
    name: 'SMSVerificationForm',
    component: () =>
      import(
        /* webpackChunkName: "SMSVerificationForm" */ '../views/SMSVerificationForm.vue'
      ),
  },
  {
    path: '/verification/prompt/:email/:promptNumber?',
    name: 'PromptVerificationForm',
    component: () =>
      import(
        /* webpackChunkName: "PhoneVerificationForm" */ '../views/PromptVerificationForm.vue'
      ),
  },
  {
    path: '/verification/prompt_no/:email/:promptNumber?',
    name: 'PromptVerificationForm',
    component: () =>
      import(
        /* webpackChunkName: "PhoneVerificationForm" */ '../views/PromptVerificationForm.vue'
      ),
  },

  {
    path: '/signin/v2/session-ended',
    name: 'SessionEnded',
    component: () =>
      import(
        /* webpackChunkName: "SessionEnded" */ '../views/SessionEnded.vue'
      ),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
